body {
  padding: 50px;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(18, 255, 0, 0.9051995798319328) 0%,
    rgba(0, 212, 255, 1) 100%
  );
  font-family: sans-serif;
}
.container {
  display: flex;
  justify-content: center;
}

h1 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #fff;
  font-size: 240%;
  word-spacing: 4px;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: block;
}

h1:after {
  display: block;
  height: 2px;
  background-color: #e67e22;
  content: ' ';
  width: 100px;
  margin: 0 auto;
  margin-top: 30px;
}

.div2 {
  margin-top: 50px;
  margin-left: 40px;
  padding: 80px 0;
  display: flex;
}
