.todoListMain .header input {
  padding: 10px;
  font-size: 20px;
  border: 1px solid #fff;
  width: 350px;
  height: 30px;
  border-radius: 10px;
  margin-left: 20px;
}

.todoListMain .header input:hover {
  border: 2px solid #cf6d17;
}

.todoListMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  margin-right: 0px;
  background-color: #e67e22;
  color: #fff;
  border: 2px solid #e67e22;
  border-radius: 10px;
  width: 100px;
  height: 50px;
}
.todoListMain .header button:hover {
  background-color: #cf6d17;
  border: 2px solid #cf6d17;
  cursor: pointer;
}
.todoListMain .theList {
  margin-bottom: 20px;
  padding-bottom: 20px;
  list-style: none;
  padding-left: 0;
  width: auto;
}
.todoListMain .theList li {
  color: #333;
  background-color: #fff;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 10px;
  list-style: none;
  font-size: 30px;
  transition: background-color, 2s ease-out;
}

.todoListMain .theList li:hover {
  background-color: #e67e22;
  cursor: pointer;
}

ul.theList {
  padding: 0;
}
